.vux-close {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  color: #999;
  width: 24px;
  height: 24px;
}
.vux-close:before,
.vux-close:after {
  content: '';
  position: absolute;
  left: 0;
  top: 11px;
  width: 24px;
  height: 1px;
  background-color: currentColor;
  transform: rotate(-45deg);
}
.vux-close:after {
  transform: rotate(45deg);
}
.ximg-demo {
  width: 100%;
  height: auto;
  display: block;
}
@keyframes rotating {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
.dazhuanpan {
  animation: rotating 10s linear infinite;
}
@keyframes scale {
  from {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  to {
    transform: scale(1);
  }
}
.dazhuanpan-go {
  animation: scale 2s linear infinite;
}
.dialog-demo.video .weui-dialog {
  width: 100%;
  max-width: 100%;
  background-color: transparent;
}
.dialog-demo.video #video-1 {
  width: 100%;
}
.dialog-demo.yiyuan .weui-dialog {
  background-color: transparent;
}



